import React from "react";

export interface IGameContextProps {
  isInGame: boolean;
  setInGame: (InGame: boolean) => void;

  isPlayerTurn: boolean;
  setPlayerTurn: (turn: boolean) => void;
  isGameStarted: boolean;
  setGameStarted: (started: boolean) => void;
  isMultiplayer: boolean;
  setMultiplayer: (turn: boolean) => void;
  isGameTypeDecided: boolean;
  setGameTypeDecided: (turn: boolean) => void;
  isGameDisconnected: boolean;
  setGameDisconnected: (turn: boolean) => void;

  roomName: string,
  setRoomName: (name: string) => void;
}

const defaultState: IGameContextProps = {
  isInGame: false,
  setInGame: () => {},

  isPlayerTurn: false,
  setPlayerTurn: () => {},
  isGameStarted: false,
  setGameStarted: () => {},

  isMultiplayer: false,
  setMultiplayer: () => {},
  isGameTypeDecided: false,
  setGameTypeDecided: () => {},
  isGameDisconnected: false,
  setGameDisconnected: () => {},
  roomName: "",
  setRoomName: () => {}
}

export default React.createContext(defaultState);
