import React, { useContext, useState } from "react";
import styled from "styled-components";
import gameContext from "../../gameContext";
import gameService from "../../services/gameService";
import socketService from "../../services/socketService";

interface IJoinGameProps {}

const JoinGameContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2em;
`;

const RoomIdInput = styled.input`
  height: 30px;
  width: 20em;
  font-size: 17px;
  outline: none;
  border: 1px solid #4478ad;
  border-radius: 3px;
  padding: 0 10px;
`;

const JoinButton = styled.button`
  outline: none;
  background-color: #4478ad;
  color: #fff;
  font-size: 17px;
  border: 2px solid transparent;
  border-radius: 5px;
  padding: 4px 18px;
  transition: all 230ms ease-in-out;
  margin-top: 1em;
  cursor: pointer;

  &:hover {
    background-color: transparent;
    border: 2px solid #4478ad;
    color: #4478ad;
  }
`;

export function JoinGame(props: IJoinGameProps) {
  const [isJoining, setJoining] = useState(false);

  const { setInGame, isInGame } = useContext(gameContext);
  const { roomName, setRoomName } = useContext(gameContext);

  const handleRoomNameChange = (e: React.ChangeEvent<any>) => {
    const value = e.target.value;
    setRoomName(value);
  };

  const JoinGame = async (e: React.FormEvent) => {
    e.preventDefault();
    e.stopPropagation();

    const socket = socketService.socket;
    if (!roomName || roomName.trim() === "" || !socket) return;

    setJoining(true);

    const joined = await gameService
      .joinGameRoom(socket, roomName)
      .catch((err) => {
        alert(err);
      });

    if (joined) setInGame(true);

    setJoining(false);
   
  };

  return (
    
    <form onSubmit={JoinGame}>
      <JoinGameContainer>
      <h4 className="text-sm text-gray-500 dark:text-gray-300">  </h4>
        <RoomIdInput
          placeholder=" 1"
          value={roomName}
          onChange={handleRoomNameChange}
        />

        <JoinButton type="submit" disabled={isJoining}>
          {isJoining ? " ..." : " "}
        </JoinButton>
      </JoinGameContainer>
    </form>
  );
}
