import React, { useContext, useState } from "react";
import styled from "styled-components";
import gameContext from "../../gameContext";

export function ChooseGameType() {

//const [isMultiplayer, setMultiplayer] = useState(false);
//const [isGameTypeDecided, setGameTypeDecided] = useState(false);

const { setGameTypeDecided, isGameTypeDecided } = useContext(gameContext);
const { setMultiplayer, isMultiplayer } = useContext(gameContext);



const handleChoosedGameType = (e: React.ChangeEvent<any>) => {
    const value = e.target.value;
    setMultiplayer(value === "true");
    setGameTypeDecided(true);
  };

const ChooseGameContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 4em;
`;

const ChooseGameButton = styled.button`
  outline: none;
  background-color: #4478ad;
  color: #fff;
  font-size: 17px;
  border: 2px solid transparent;
  border-radius: 5px;
  padding: 4px 18px;
  transition: all 230ms ease-in-out;
  margin-top: 1em;
  cursor: pointer;
  width: 30%;

  &:hover {
    background-color: transparent;
    border: 2px solid #4478ad;
    color: #4478ad;
  }
`;

    return (
    
        <form onSubmit={ChooseGameType}>
          <ChooseGameContainer>
          <h4 className="text-sm text-gray-500 dark:text-gray-300">  </h4>

            <ChooseGameButton onClick={handleChoosedGameType} value="false"  type="button" >
              {" "}
            </ChooseGameButton>


            <ChooseGameButton onClick={handleChoosedGameType} value="true" type="button">
              {"  "}
            </ChooseGameButton>
          </ChooseGameContainer>
        </form>
      );

}