import { getStatuses } from '../../../lib/multiplayer/statuses'
import { Key } from '.././multiplayer/Key'
import { useEffect } from 'react'
import { ENTER_TEXT, DELETE_TEXT } from '../../../constants/strings'

type Props = {
  onChar: (value: string) => void
  onDelete: () => void
  onEnter: () => void
  guesses: string[]
  isRevealing?: boolean
  isPlayerTurn: boolean,
  solution: string
}

export const Keyboard = ({
  onChar,
  onDelete,
  onEnter,
  guesses,
  isRevealing,
  isPlayerTurn,
  solution
}: Props) => {
  const charStatuses = getStatuses(guesses, solution)

  const onClick = (value: string) => {
    if (value === 'ENTER') {
      onEnter()
    } else if (value === 'DELETE') {
      onDelete()
    } else {
      onChar(value)
    }
  }

  useEffect(() => {
    const listener = (e: KeyboardEvent) => {
      if(!isPlayerTurn) return;
      if (e.code === 'Enter') {
        onEnter()
      } else if (e.code === 'Backspace') {
        onDelete()
      } else {
        const key = e.key.toUpperCase()
        if (key.length === 1 && ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '','', '', '', '', '', '', '', '', '', '', ''].includes(key)) {
          onChar(key)
        }
      }
    }
    window.addEventListener('keyup', listener)
    return () => {
      window.removeEventListener('keyup', listener)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onEnter, onDelete, onChar])

  return (
    <div>
      <div className="flex justify-center mb-1">
        {['', '', '', '', '', '', '', '', '', '', ''].map((key) => (
          <Key
            value={key}
            key={key}
            onClick={onClick}
            status={charStatuses[key]}
            isRevealing={isRevealing}
            isPlayerTurn={isPlayerTurn}
          />
        ))}
      </div>
      <div className="flex justify-center mb-1">
        {['', '', '', '', '', '', '', '', '', '', ''].map((key) => (
          <Key
            value={key}
            key={key}
            onClick={onClick}
            status={charStatuses[key]}
            isRevealing={isRevealing}
            isPlayerTurn={isPlayerTurn}
          />
        ))}
      </div>
      <div className="flex justify-center">
        <Key width={65.4} value="ENTER" onClick={onClick} isPlayerTurn={isPlayerTurn}>
          {ENTER_TEXT}
        </Key>
        {['', '', '', '', '', '', '', ''].map((key) => (
          <Key
            value={key}
            key={key}
            onClick={onClick}
            status={charStatuses[key]}
            isRevealing={isRevealing}
            isPlayerTurn={isPlayerTurn}
          />
        ))}
        <Key width={65.4} value="DELETE" onClick={onClick} isPlayerTurn={isPlayerTurn}>
          {DELETE_TEXT}
        </Key>
      </div>
    </div>
  )
}
